import { useEffect, useState } from "react";
import Cookie from "cookie-universal";
import axios from "axios";
import { Link } from "react-router-dom";
import { baseURI, CAT, CATEGORIES } from "../../../Api/Api";
import TableShow from "../../../Components/Dashboard/TableShow";
export default function Categories() {
  const [categories, setcategories] = useState([]);
  const [limit,setlimit] = useState(5) ;
  const [page,setpage]=useState(1)
  const cookie = Cookie();
  const token = cookie.get("e-commerce");
  async function handledelete(id) {
    try {
      const res = await axios.delete(`${baseURI}/${CAT}/${id}`, {
        headers: {
          Authorization: "Bearer " + token,
        },
      });
      setcategories((prev) => prev.filter((item) => item.id !== id));
    } catch (err) {
      console.log(err);
    }
  }
  useEffect(() => {
    axios
      .get(`${baseURI}/${CATEGORIES}`, {
        headers: {
          Authorization: "Bearer " + cookie.get("e-commerce"),
        },
      })
      .then((data) => {
        setcategories(data.data);
      })
      .catch((err) => console.log(err));
  }, []);
  const header = [
    { key: "title", name: "Title" },
    { key: "image", name: "Image" },
  ];
  return (
    <div className="bg-white p-2 w-100">
      <div className="d-flex align-items-center justify-content-between">
        <h1>Categories Page</h1>
        <Link to="/dashboard/category/add" className="btn btn-primary">
          Add Category
        </Link>
      </div>
      <TableShow
        header={header}
        data={categories}
        delete={handledelete}
        limit={limit}
        page={page}
        setpage={setpage}
        setlimit={setlimit}
      />
      
    </div>
  );
}
