import { useEffect, useState } from "react";
import { baseURI, USER, USERS } from "../../../Api/Api.js";
import axios from "axios";
import Cookie from "cookie-universal";
import { Link } from "react-router-dom";
import TableShow from "../../../Components/Dashboard/TableShow.js";
export default function Users() {
  const [users, setusers] = useState([]);
  const [currentuser, setcurrentuser] = useState("");
  const[page,setpage]=useState(1)
  const [limit,setlimit] = useState(5) ;
  const cookie = Cookie();
  const token = cookie.get("e-commerce");
  async function handledelete(id) {
    try {
      const res = await axios.delete(`${baseURI}/${USER}/${id}`, {
        headers: {
          Authorization: "Bearer " + token,
        },
      });
      setusers((prev) => prev.filter((item) => item.id !== id));
    } catch (err) {
      console.log(err);
    }
  }
  const headers = [
    {
      key: "name",
      name: "Username",
    },
    {
      key: "email",
      name: "Email",
    },
    {
      key: "role",
      name: "Role",
    },
  ];
  useEffect(() => {
    axios
      .get(`${baseURI}/${USERS}`, {
        headers: {
          Authorization: "Bearer " + cookie.get("e-commerce"),
        },
      })
      .then((data) => {
        setusers(data.data);
        console.log(data.data);
      })
      .catch((err) => console.log(err));
  }, []);
  useEffect(() => {
    axios
      .get(`${baseURI}/${USER}`, {
        headers: { Authorization: "Bearer " + token },
      })
      .then((res) => setcurrentuser(res.data));
  }, []);
  return (
    <div className="bg-white p-2 w-100">
      <div className="d-flex align-items-center justify-content-between">
        <h1>Users Page</h1>
        <Link to="/dashboard/user/add" className="btn btn-primary">
          Add User
        </Link>
      </div>
      <TableShow
        header={headers}
        data={users}
        delete={handledelete}
        currentuser={currentuser}
        page={page}
        limit={limit}
        setpage={setpage}
        setlimit={setlimit}
      />
    </div>
  );
}
