import {
  faPenToSquare,
  faStar,
  faTrash,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import PaginatedItems from "./Pagination/Pagination";
import { Form } from "react-bootstrap";
export default function TableShow(props) {
  const currentuser = props.currentuser || {
    name: "",
  };

  //استخدام الباغينيت من اجل عرض عدد عناصر محدد في كل صفحة والتنقل بين الصفحات
  const start = (props.page - 1) * props.limit;
  const end = Number(start) + Number(props.limit); //قسرناها لرقم لانو عند مامنختار الرقم من السيليكت رح يختارو كسترينغ وبالتالي رح يضرب ويجيب كل العناصر لهيك صرناه لرقم وبيمشي حالو
  const final = props.data.slice(start, end);
  const headershow = props.header.map((item, key) => (
    <th key={key}>{item.name}</th>
  ));
  const datashow = final.map((item, key) => (
    <tr key={key}>
      <td>{item.id}</td>
      {props.header.map((item2, key2) => (
        <td key={key2}>
          {item2.key === "image" ? (
            <img width="50px" src={item[item2.key]} alt="im" />
          ) : item2.key === "images" ? (
            <div className="d-flex align-items-center justify-content-center gap-2 flex-wrap">
              {item[item2.key].map((img) => (
                <img width="50px" src={img.image} alt="" />
              ))}
            </div>
          ) : item[item2.key] === "1995" ? (
            "Admin"
          ) : item[item2.key] === "2001" ? (
            "User"
          ) : item[item2.key] === "1996" ? (
            "Writer"
          ) : item[item2.key] === "1999" ? (
            "Product Manager"
          ) : (
            item[item2.key]
          )}
          {currentuser && item[item2.key] === currentuser.name && "(You)"}
          {item2.key === "price" && "$"}
          {item2.key === "rating" && (
            <FontAwesomeIcon
              icon={faStar}
              style={{ color: "gold", paddingLeft: "6px" }}
            />
          )}
        </td>
      ))}
      <td>
        <div className="d-flex align-items-center justify-content-center gap-2 ">
          <Link to={`${item.id}`}>
            <FontAwesomeIcon fontSize={"19px"} icon={faPenToSquare} />
          </Link>
          {currentuser.name !== item.name && (
            <FontAwesomeIcon
              fontSize={"19px"}
              color="red"
              icon={faTrash}
              onClick={() => {
                props.delete(item.id);
              }}
              cursor={"pointer"}
            />
          )}
        </div>
      </td>
    </tr>
  ));
  return (
    <>
      <table className="tableStyle">
        <thead>
          <tr>
            <th>Id</th>
            {headershow}
            <th style={{ textAlign: "center" }}>Action</th>
          </tr>
        </thead>
        <tbody>
          {props.data.length === 0 && (
            <tr>
              <td colSpan={12} className="text-center">
                Loading.....
              </td>
            </tr>
          )}
          {datashow}
        </tbody>
      </table>
      <div className="d-flex align-items-center justify-content-end flrx-wrap">
        <div className="col-1">
          <Form.Select
            onChange={(e) => props.setlimit(e.target.value)}
            aria-label="Default select example"
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="15">15</option>
          </Form.Select>
        </div>
        <PaginatedItems
          setpage={props.setpage}
          itemsPerPage={props.limit}
          data={props.data}
        />
      </div>
    </>
  );
}
